<template>
    <v-list>
        <v-combobox
            placeholder="Search..."
            v-model="current_search" 
            prepend-icon="search" 
            :items="component_names"
            @keydown.enter.native="tryOpen"
        ></v-combobox>
        <v-expansion-panel :value="1">
            <v-expansion-panel-content :v-model="true">
            <div slot="header">Components</div>
            <v-list>
                <v-list-tile :to="`/components/${element.name}`" v-for="(element, i) in components" :key="i">
                    <v-list-tile-title>{{ element.name }}</v-list-tile-title>
                </v-list-tile>
            </v-list>
            </v-expansion-panel-content>
        </v-expansion-panel>
    </v-list>
</template>

<script>
    import load from "../scripts/load.js"

    export default {
        name: "navigation",
        created() {
            load("components", "/components.json");
        },
        data() {
            return {
                current_search: ""
            };
        },
        computed: {
            components() {
                return this.$store.state.components;
            },
            component_names() {
                let tmp = [];
                this.components.forEach(element => {
                    tmp.push(element.name);
                });
                return tmp;
            }
        },
        methods: {
            tryOpen() {
                let search = this.components.filter(e => e.name.includes(this.current_search || ""));
                console.log(search)
                if(search.length > 0) {
                    this.$router.push({ path: `/components/${search[0].name}` });
                    this.current_search = "";
                }
            }
        }
    }
</script>

<style scoped>
    .v-input {
        padding: 0px 12px;
    }
</style>
