<template>
    <span>
        <v-card-title v-if="show_header">
            <span>{{ struc.name }}</span>
            <v-spacer></v-spacer>
            <v-text-field
                v-model="search"
                prepend-icon="search"
                label="Search..."
                single-line
                hide-details
                v-if="struc.initial"
            ></v-text-field>
        </v-card-title>      
            
        <v-data-table
            :search="search"
            :items="structure_array"
            :headers="headers"
            :hide-actions="true"
            no-data-text="This component has no arguments."
            class="elevation-1"
        >
            <template slot="headers" slot-scope="props">
                <tr>
                    <th v-for="(header, i) in headers" :key="i">
                        {{ header.text }}
                    </th>
                </tr>
            </template>
            <template slot="items" slot-scope="props">
                <tr>
                    <td>{{ props.item.argument }}</td>
                    <td>{{ props.item.type }}</td>
                    <td>{{ props.item.default }}</td>
                    <td>{{ props.item.description }}</td>
                </tr>
            </template>
            <v-alert slot="no-results" :value="true" color="error" icon="warning">
                No results for "{{ search }}"
            </v-alert>
        </v-data-table>
    </span>
</template>

<script>
export default {
    name: "struc-table",
    props: {
        struc: Object,
        headers: Array,
        structure_array: Array,
        show_header: {
            type: Boolean,
            default: true
        }
    },
    data() {
        return {
            search: ""
        }
    }
}
</script>