<template>
    <v-card v-if="show_data">
        <v-card-title primary-title>
            <div class="headline">
                {{ component.title.split(":")[0] }}:<span class="font-weight-light">{{ component.title.split(":")[1] }}</span>
            </div>
            <v-spacer/>

            <v-tooltip left>
                <v-btn slot="activator" flat icon @click.stop="to_clipboard">
                    <v-icon color="primary">assignment</v-icon>
                </v-btn>
                <span>Copy Component</span>
            </v-tooltip>
        </v-card-title>
        <v-divider></v-divider>
        <v-layout style="padding: 0.2em 16px;" v-if="component.description">
            {{ component.description }}
        </v-layout>
        <v-divider></v-divider>
        <v-expansion-panel expand>
            <component-structure :structure="component.structure"/>
            <component-problems :problems="component.known_problems"/>

            <v-expansion-panel-content v-if="component.example">
                <div slot="header">Code Example</div>
                <v-container>
                    <v-card color="primary">
                        <v-card-title class="card-title">
                            <div class="title">Example</div>
                            <v-spacer></v-spacer>
                            <v-tooltip left>
                                <v-btn slot="activator" flat icon @click.stop="to_clipboard(JSON.stringify(component.example, null, '\t'))">
                                    <v-icon color="white">assignment</v-icon>
                                </v-btn>
                                <span>Copy Example</span>
                            </v-tooltip>
                        </v-card-title>
                        <v-divider></v-divider>
                        <code v-html="JSON.stringify(component.example, null, '  ').replace(/\n/g, '<br>')"/>
                    </v-card>
                </v-container>
                
            </v-expansion-panel-content>   
        </v-expansion-panel>
    </v-card>
    <v-progress-linear :indeterminate="true" v-else></v-progress-linear>
</template>

<script>
import load from "../scripts/load.js";
import ComponentStructure from "../components/component/Structure.vue";
import ComponentProblems from "../components/component/Problems.vue";

export default {
    components: {
        ComponentStructure,
        ComponentProblems
    },
    created() {
        this.helper = (component_info) => {
            if(!this.component_info) {
                window.setTimeout(this.helper, 100);
            } else {
                load(this.component_info.name, this.component_info.link);
            }
        }
        this.helper();
    },
    data() {
        return {
            helper: null
        };
    },
    computed: {
        show_data() {
            return this.component_info != undefined && this.component != undefined;
        },
        component() {
            return this.$store.state[this.component_info.name];
        },
        component_info() {
            let comp = this.$route.params.name;
            return this.all_components.filter(e => e.name == comp)[0];
        },
        all_components() {
            return this.$store.state.components;
        }
    },
    methods: {
        to_clipboard(text) {
            navigator.permissions.query({name: "clipboard-write"})
            .then(result => {
                if (result.state == "granted" || result.state == "prompt") {
                    if(typeof text != "string") text = JSON.stringify({ [this.component_info.name]: this.get_default(this.component.structure) }, null, "\t");
                    navigator.clipboard.writeText(text);
                }
            });
        },
        get_default(structure, first=true) {
            if(!structure) return;
            if(!first && (structure.structure == undefined || (structure.type != "Object" && structure.type != "Array"))) {
                if(Number.isNaN(Number(structure.default))) return structure.default || "-";
                return Number(structure.default);
            }
            
            let def = {};
            let it = structure;
            if(!first) it = structure.structure;
            
            for(let key in it) {
                console.log(key)
                def[key] = this.get_default(it[key], false);
            }

            return def;
        }
    },
    watch: {
        '$route' (to, from) {
            load(this.component_info.name, this.component_info.link);
        }
    }
}
</script>

<style scoped>
    code {
        width: 100%;
    }
    .card-title {
        padding: 0.3em;
        color: white;
    }
</style>
