<template>
    <v-expansion-panel-content v-if="problems.length > 0">
        <div slot="header">Known Problems</div>
            <v-container fluid grid-list-sm>
                <v-layout row wrap>
                    <v-flex v-for="(prob, i) in display_problems" :key="i" xs4 class="lg5-custom">
                        <v-card class="shadow-card" :flat="false">
                            <v-toolbar class="card-bar" height="30px" flat :color="prob.category">
                                <v-icon style="color: white;">{{ icons[prob.category] }}</v-icon>
                                <!-- <v-spacer></v-spacer> -->
                                <div style="color: white;" class="headline">
                                    {{ names[prob.category] }}
                                </div>
                            </v-toolbar>
                            <v-card-title primary-title>
                                <v-subheader v-if="prob.version">{{ prob.version }}</v-subheader>
                                <span :class="`card-content${!prob.version ? ' big': ''}`">{{ prob.description }}</span>
                            </v-card-title>
                        </v-card>
                    </v-flex>
                </v-layout>
            </v-container>
    </v-expansion-panel-content>
</template>

<script>
export default {
    name: "component-problems",
    props: {
        problems: {
            type: Array,
            default() {
                return []
            }
        }
    },
    data() {
        return {
            names: {
                info: "Problem",
                success: "Quirk",
                warning: "Assert",
                error: "Crash"
            },
            icons: {
                info: "info",
                success: "check_circle",
                warning: "error",
                error: "cancel"
            },
            sort: {
                info: 2,
                success: 3,
                warning: 1,
                error: 0
            }
        }
    },
    computed: {
        display_problems() {
            return this.problems.sort((a, b) => {
                let s1 = this.sort[a.category];
                let s2 = this.sort[b.category];
                if(s1 > s2) return 1;
                if(s1 < s2) return -1;
                return 0;
            });
        }
    },
    methods: {
        style(color_name) {
            return `
                background-color: ${this.colors[color_name]};
                color: white;
                margin-left: 0.2em;
                padding: 12px;
            `.replace(/\t|\n/g, "");
        },
        random() {
            return Math.ceil(Math.random() * 8) + 4;
        }
    }
}
</script>

<style scoped>
    .shadow-card {
        box-shadow: 0px 2px 4px -1px rgba(0,0,0,0.2), 0px 4px 5px 0px rgba(0,0,0,0.14), 0px 1px 10px 0px rgba(0,0,0,0.12) !important; 
    }
    .v-subheader {
        height: 20px;
        padding: 0;
    }
    @media (min-width: 1264px) and (max-width: 1903px) {
        .flex.lg5-custom {
            width: 20%;
            max-width: 20%;
            flex-basis: 20%;
        }
    }

    .card-content {
        height: 100px;
        overflow-y: auto;
    }
    .card-content.big {
        height: 120px;
        overflow-y: auto;
    }
    .card-content::-webkit-scrollbar {
        width: 3px;
        height: 3px;
    }
</style>
<style>
    .card-bar * {
        padding-left: 4px;
    }
</style>
