<template>
    <v-expansion-panel-content :v-model="true">
        <div slot="header">Component Structure</div>
        <v-container>
            <v-card v-for="(struc, i) in comp_structs" v-if="struc.initial" :key="i">
                <struc-table :headers="headers" :struc="struc" :structure_array="structure_array(struc.obj)"></struc-table>
                <v-divider v-if="comp_structs.length > 1" class="divider"></v-divider>
            </v-card>
            <v-expansion-panel class="margin" v-else>
                <v-expansion-panel-content>
                    <div slot="header">{{ struc.name }}</div>
                    <v-card>
                        <struc-table
                            :show_header="struc.initial" 
                            :headers="headers" 
                            :struc="struc"
                            :structure_array="structure_array(struc.obj)"
                        />
                    </v-card>
                </v-expansion-panel-content>
            </v-expansion-panel>
        </v-container>
    </v-expansion-panel-content>
</template>

<script>
import StrucTable from "./StrucTable";

export default {
    name: "component-structure",
    components: {
        StrucTable
    },
    props: {
        structure: {
            type: Object,
            default() {
                return {
                    
                }
            }
        }
    },
    data() {
        return {
            search: "",
            headers: [
                {
                    text: "Argument",
                    value: "argument"
                },
                {
                    text: "Type",
                    value: "type"
                },
                {
                    text: "Default",
                    value: "default"
                },
                {
                    text: "Description",
                    value: "description"
                }
            ],
            comp_structs: [
                {
                    name: "Arguments",
                    obj: this.structure,
                    initial: true
                }
            ]
        }
    },
    computed: {
        
    },
    methods: {
        string(arg) {
            if(Array.isArray(arg)) return arg.join(", ");
            if(Number.isNaN(Number(arg))) return arg || "-";
            return arg;
        },
        structure_array(obj) {
            let arr = [];

            for(let key in obj) {
                arr.push({
                    argument: key,
                    type: obj[key].minecraft_type ? `${this.string(obj[key].type)}<${this.string(obj[key].minecraft_type)}>` : this.string(obj[key].type),
                    default: this.string(obj[key].default),
                    description: this.string(obj[key].description),
                });

                if(obj[key].structure != undefined && !this.contains(key, this.comp_structs)) {
                    this.comp_structs.push({
                        name: key,
                        obj: obj[key].structure,
                        initial: false
                    });
                }
            }

            return arr;
        },
        contains(name, arr) {
            for(let i = 0; i < arr.length; i++) {
                if(arr[i].name == name) return true;
            }
            return false;
        }
    },
    watch: {
        structure() {
            this.comp_structs = [
                {
                    name: "Arguments",
                    obj: this.structure,
                    initial: true
                }
            ]
        }
    }
}
</script>

<style scoped>
    .margin {
        margin-top: 1em;
    }
    .divider {
        margin: 2em;
    }
</style>