import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Component from "./views/Component.vue";

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/components/:name',
      name: 'Component',
      component: Component
    }
  ]
})
