import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    components: []
  },
  mutations: {
    setData(state, payload) {
      if(Array.isArray(payload.data)) {
        Vue.set(state, payload.key, payload.data.sort())
      } else {
        Vue.set(state, payload.key, payload.data);
      }
    }
  },
  actions: {

  }
})
